.d3-tip {
  line-height: 1;
  padding: 6px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  border-radius: 4px;
  font-size: 12px;
  }
  
  /* Creates a small triangle extender for the tooltip 
  */
  .d3-tip:before {
    box-sizing: border-box;
    display: inline;
    font-size: 12px;
    width: 100%;
    line-height: 1;
    color: rgba(0, 0, 0, 0.8);
    content: "\25BC";
    position: absolute;
    text-align: center
  }

  .d3-tip.n:before {
    margin: -2px 0 0 0;
    top: 100%;
    left: 0;
  }
  
